	module.exports = {"data":{"wordpressPost":{"title":"Would You Pay $30 For Cotton Pads?","content":"<p>Okay, so it’s a bit of an indulgence, but many years I have been using <a href=\"http://www.chanel.com/en_US/fragrance-beauty/skincare-cleansers-%26-toners-le-coton-139111\">Chanel’s Le Coton luxurious cotton pads</a>. Granted, I am a bit discerning when it comes to beauty products and often spend way more than one should.<br />\nMy friends know this and so when they discover I am not using the typically humble cotton pad but the luxe version they immediately want to know &#8211; <strong>is the box of 100 pads really worth $30</strong>.</p>\n<p>And this is what I tell them. Chanel tout Le Coton is referred to as the “highest-quality cosmetic cotton pads” and for good reason. The exquisite and ultra-soft pads are more generous in size than the average pad. This means it holds more product and covers a wider area of your face. The outside layer is made from handpicked Egyptian Cotton and <strong>inside filled with elastic fibres from Australia</strong> (bet you didn&#8217;t know that).<br />\nThis sumptuous combination makes the pads ultra-absorbent and increases the effectiveness of your daily cleansing ritual whilst immersing you in a sense of pure luxury every day.</p>\n<p>I stumbled upon these deluxe cotton pads on a shopping spree in <a href=\"http://shop.nordstrom.com/s/chanel-le-coton/3370763\">Nordstrom’s</a> in the U.S. I remember the first time I used one. I felt like a princess using a cotton pad embossed with the <strong>classic Chanel logo</strong>. I immersed the pad in toner and the moment it touched my skin I knew I’d never use anything else. It was so soft, so gentle and so indulgent. Plus, my skin instantly looked and felt better. To this day, I have changed many skin care products over the years, however, Chanel’s Le Coton has been my constant beauty companion and that will never change.</p>\n<figure id=\"attachment_1953\" style=\"width: 275px\" class=\"wp-caption aligncenter\"><img class=\"size-medium wp-image-1953\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/05/10224637/Swisspers-JPG-275x300.jpg\" alt=\"\" width=\"275\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/05/10224637/Swisspers-JPG-275x300.jpg 275w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/05/10224637/Swisspers-JPG.jpg 478w\" sizes=\"(max-width: 275px) 100vw, 275px\" /><figcaption class=\"wp-caption-text\"><strong><em>Budget focused folk, or even those who are cashed up but just prefer to spend their money on other stuff, can try Swisspers.</em></strong></figcaption></figure>\n<p>&nbsp;</p>\n<p><strong>Budget Friendly Alternative</strong><br />\nThe next best thing? Although not quite made of Egyptian Cotton, <a href=\"https://www.priceline.com.au/brand/swisspers/swisspers-cotton-squares-150-pack\">Swisspers Cotton Squares</a> are wonderful and better still, available from Priceline at just $7.20 for a box of 150.<br />\nThis version is composed of a cotton and wool blend encased in a non-woven cover for a flawless cleansing ritual.<br />\nUse with eye makeup remover, cleansing oil and toner for best results.</p>\n<p>Alexandra Valentino is a writer who is just that little bit obsessed with all things fashion and beauty. You can follow her on <a href=\"https://www.instagram.com/worshipmycouture/\">Instagram here</a>.</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/05/10224239/Chanel-pads-JPG.jpg"},"categories":[{"slug":"skincare","name":"Skincare"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"b16535fd-a2df-5500-b221-9913e3bd1274"}}

